import React, { Component } from "react";
import Meme1 from "./data/meme1.jpg";
import "./styles/memes.css";
import Meme2 from "./data/meme2.jpg";
import Meme3 from "./data/meme3.jpg";
import Meme4 from "./data/meme4.jpg";
import Meme5 from "./data/meme5.jpg";
import Meme6 from "./data/meme6.jpg";

class Memes extends Component {

  render() {

    return (

      <div>
        <h2 id="title">Memes</h2>
        
        <img src={Meme1} alt=""></img>
        <img src={Meme2} alt=""></img>
        <img src={Meme3} alt=""></img>
        <img src={Meme4} alt=""></img>
        <img src={Meme5} alt=""></img>
        <img src={Meme6} alt=""></img>
      
      </div>
    )
  }
}
 
export default Memes;